/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #000911;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}
body .tama単o-alerta {
  font-size: 2rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.filtro {
  display: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.contenedor-app {
  position: static;
}
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: 1fr 2fr;
    min-height: 10vh;
  }
}
.contenedor-app .imagen {
  position: relative;
  background-image: url(../img/logo.png);
  background-size: cover;
  background-position: center center;
  overflow: hidden;
  height: 30rem;
}
.contenedor-app .imagen::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
  z-index: 1;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
    border-radius: 1rem;
    margin-left: 1rem;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem 1rem;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
  .contenedor-app .app.app::-webkit-scrollbar {
    display: none;
  }
}

.logo-pagina {
  color: #f53756;
  margin-top: 1.8rem;
}
.logo-pagina:hover {
  -webkit-text-stroke: 0.6px #FFFFFF;
  -webkit-text-fill-color: #f53756;
}

.barra {
  margin: 5rem 0;
  margin-bottom: 1rem;
}
.barra span {
  color: #f53756;
  font-size: 2.5rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
.barra .boton {
  text-align: left;
  background-color: #000911;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  border: 1px solid #FFFFFF;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-around;
    gap: 1.5rem;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

@media (min-width: 768px) {
  .contenedor,
  .navegacion-footer {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row-reverse;
    gap: 2rem;
  }
}
.navegacion-footer {
  padding: 2rem 2rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
}

.footer {
  text-align: center;
}
.footer p {
  font-size: 1.4rem;
  color: grey;
}
.footer a {
  font-size: 1.4rem;
  color: grey;
}
.footer a:hover {
  -webkit-text-stroke: 0.6px #FFFFFF;
  -webkit-text-fill-color: #f53756;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}
.seccion.error p {
  margin-top: 1rem;
}

#iniciar-sesion {
  font-size: 4rem;
  margin-bottom: 10rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  #iniciar-sesion {
    margin-top: -10rem;
  }
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.ocupadas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
}
.ocupadas li p {
  color: rgb(255, 255, 255);
}
.ocupadas li span {
  color: rgb(112, 112, 112);
}
@media (max-width: 820px) {
  .ocupadas {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 410px) {
  .ocupadas {
    grid-template-columns: 1fr;
  }
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 400ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio.seleccionado:hover {
  cursor: zoom-out;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
  cursor: zoom-in;
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  margin: 0;
}

.contenido-resumen {
  padding: 3rem 0;
}
.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}
.contenido-resumen h3 {
  margin-top: 5rem;
}

.contenedor-servicio {
  border-bottom: 1px solid #e1e1e1;
}

.total {
  color: #f53756;
  font-size: 4rem;
  text-align: center;
}
.total span {
  font-size: 4rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
  font-size: 3rem;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas .total, .servicios .total {
  text-align: center;
  color: #f53756;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .boton-eliminar {
  margin: 1rem 0;
  font-size: 2rem;
}

.boton.amarillo {
  background-color: #FFCE00;
  color: #1a1b15;
}
.boton.amarillo:hover {
  background-color: green;
  color: #FFFFFF;
}

.no-hay-citas {
  background-color: rgb(65, 63, 63);
  padding: 1rem;
  color: #f53756;
  -webkit-text-stroke: 0.6px #FFFFFF;
  border-top-left-radius: 10rem;
  border-top-right-radius: 10rem;
  font-size: 2.2rem;
}

@media (min-width: 768px) {
  .no-hay-citas {
    margin-top: 10rem;
    font-size: 3rem;
  }
}
.formulario {
  margin-top: 5rem;
  width: 100%;
}
.formulario span {
  color: #f53756;
  font-weight: 700;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #898e6e;
  color: #FFFFFF;
  cursor: not-allowed;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  border: 1px solid #FFFFFF;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  border: 1px solid #FFFFFF;
  cursor: pointer;
}

@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-around;
    margin-top: 20rem;
    gap: 4rem;
  }
}
.acciones span {
  color: #f53756;
  font-weight: 700;
}
.acciones a {
  color: #FFFFFF;
  display: block;
  font-size: 2rem;
  margin-bottom: 2.5rem;
  text-align: center;
  font-size: 1.5rem;
}
.acciones a.verde {
  background-color: green;
}
.acciones a:hover span {
  color: #329f00;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  display: block;
  margin: 2rem 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
  border-radius: 1rem;
}
.alerta.exito {
  background-color: #329f00;
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 500ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

button:nth-child(1) {
  border-top-left-radius: 2rem;
}

button:nth-child(3) {
  border-top-right-radius: 2rem;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin: 0;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

input.formulario {
  padding: 1rem;
  margin: 0;
  border-radius: 1rem;
}

.ocultar {
  visibility: hidden;
}